#home {
  margin-bottom: 30px;
  color: #ffffff;
  background-image: linear-gradient(rgba(10, 10, 10, 0.45), rgba(10, 10, 10, 0.45)),url(/static/media/nature-road-truck-field-wallpaper-preview.f6179b76.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 99vw;
  height: 100vh;
  display: flex;
  position: relative; 
  display:flex;
  align-self:flex-start;
}

#home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Adjust the opacity (last value) as needed */
  z-index: -1; /* Place the overlay behind the content */
}


.parallax-container {
  background-image:linear-gradient(rgba(10, 10, 10, 0.45), rgba(10, 10, 10, 0.45)), url(/static/media/nature-road-truck-field-wallpaper-preview.f6179b76.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh; 
  width: 100vw;
  overflow: hidden;
}

.parallax-content {
  height: 100%; 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
}

